export const initialState = {
  projects: [],
};
export const Reducer = (state, action) => {
  console.log(action);
  
  switch (action.type) {
    case "get_user_projects":
      return { ...state, projects: action.payload };
    case "update_user_projects":
      return { ...state, projects: action.payload };
    case "get_project":
      return { ...state, userProject: action.payload };
    case "update_project":
      return { ...state, userProject: action.payload };

    default:
      return state;
  }
};

